"use client";

import { motion } from "framer-motion";
import Image from "next/image";

export default function HeroSection() {
  return (
    <section className="relative w-full min-h-[90vh] overflow-hidden flex items-center">

      {/* FIX 1 — Background container full screen height */}
      <div className="absolute inset-0">
        <Image
          src="/hero2.png"
          alt="Dental Clinic Background"
          fill
          priority
          className="
            object-cover object-right
          "
        />
      </div>

      {/* FIX 2 — Soft blending background so top/bottom never look white */}
      <div className="absolute inset-0 bg-gradient-to-r from-[#eaf2ff]/95 via-white/70 to-white/20 z-10"></div>

      {/* LEFT CONTENT BLOCK */}
      <div className="relative z-20 flex items-center w-full lg:w-1/2 px-4 md:px-12 lg:px-16 py-16">

        <motion.div
          initial={{ opacity: 0, y: 25 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6 }}
          className="
            bg-white/85 backdrop-blur-lg
            shadow-[0_10px_40px_rgba(0,0,0,0.12)]
            border border-white/60
            rounded-3xl p-8 md:p-10 lg:p-12
            max-w-xl
          "
        >
          <h1 className="text-4xl md:text-5xl font-bold text-gray-900 leading-tight">
            Welcome to <br /> Smileverse Dentech
          </h1>

          <p className="mt-6 text-gray-700 text-lg leading-relaxed">
            We’re committed to transforming India’s dental care landscape
            through technology, expertise, and precision.
          </p>

          <p className="mt-4 text-gray-700 text-lg leading-relaxed">
            As the demand for expert orthodontic care rises, we created{" "}
            <span className="font-semibold text-gray-900">AlignPro</span> — our
            flagship clear aligner system built for safety, accuracy,
            and affordability.
          </p>

          <p className="mt-4 text-gray-700 text-lg leading-relaxed">
            AlignPro marks the first chapter in building a complete,
            cost-effective ecosystem of advanced dental innovation.
          </p>

          <motion.a
            initial={{ opacity: 0, y: 10 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ delay: 0.3 }}
            href="/alignpro"
            className="
              inline-block mt-8 bg-blue-600 hover:bg-blue-700
              text-white text-sm font-medium px-7 py-3
              rounded-xl transition-all shadow-lg
            "
          >
            Explore AlignPro
          </motion.a>
        </motion.div>

      </div>

      <div className="w-1/2 hidden lg:block"></div>
    </section>
  );
}
